.services-main {
  &__top {
    background: #383838;
    padding: 200px 0 30px;
    &-links {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-link {
      font-size: 14px;
      line-height: 120%;
      color: #8C8C8C;
      position: relative;
      &:hover {
        color: #fff;
        &:after {
          width: calc(100% + 20px);
        }
      }
      &:after {
        content: '';
        position: absolute;
        left: -10px;
        bottom: -10px;
        width: 0;
        height: 2px;
        background: #8C8C8C;
        transition: all 0.5s;
      }
      &--active {
        color: #fff;
        &:after {
          content: '';
          position: absolute;
          left: -10px;
          bottom: -10px;
          width: calc(100% + 20px);
          height: 2px;
          background: #8C8C8C;
        }
      }
      &:not(:first-child) {
        margin-left: 45px;
      }
    }
    &-btn {
      font-size: 14px;
      line-height: 120%;
      color: #373737;
      display: none;
      cursor: pointer;
    }
  }
  &__menu-close {
    position: relative;
    z-index: 8;
    width: 22px;
    height: 16px;
    display: none;
    &--active {
      display: block;
      top: -80px;
      right: 15px;
    }
    span {
      position: absolute;
      height: 1.8px;
      background: #EBA707;
      &:nth-child(1) {
        transform: rotate(-45deg);
        top: 50%;
        width: 100%;
      }
      &:nth-child(2) {
        transform: rotate(45deg);
        top: 50%;
        width: 100%;
      }
    }
  }
  &__menu-desc {
    &--active {
      display: none;
    }
  }
  &__menu {
    display: none;
  }
  &__scrollbar {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  &__menu-link {
    display: inline-block;
    font-size: 14px;
    line-height: 120%;
    color: #EBA707;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    &:not(:first-child) {
      margin-top: 35px;
    }
    &:hover {
      color: #fff;
      &:after {
        width: calc(100% + 20px);
      }
    }
    &:after {
      content: '';
      position: absolute;
      left: -10px;
      bottom: -6px;
      width: 0;
      height: 1px;
      background: #8C8C8C;
      transition: all 0.5s;
    }
    &--active {
      color: #fff;
      &:after {
        content: '';
        position: absolute;
        left: -10px;
        bottom: -6px;
        width: calc(100% + 20px);
        height: 1px;
        background: #8C8C8C;
      }
    }
  }
}
.services-main {
  padding: 60px 0;
  background: #282828;
  &__title {
    grid-column: 1/3;
    display: flex;
    align-items: center;
    padding-left: 25px;
  }
}
.print {
  background: #F5F5F5;
  padding: 50px 0;
  &__title {
    grid-column: 2/5;
    color: #B7B7B7;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-right: 25px;
    text-align: end;
    h2 {
      padding: 0 28px 0 0;
      &:after {
        left: 100%;
        background: #B7B7B7;
        height: calc(100% - 20px);
      }
    }
  }
}
.printing {
  padding: 50px 0;
  background: #3A3A3A;
  &__title {
    display: flex;
    align-items: center;
    grid-column: 1/3;
    color: #B7B7B7;
    padding-left: 25px;
    h2 {
      &:after {
        background: #B7B7B7;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .print {
    &__title {
      grid-column: 2/4;
    }
  }
}

@media screen and (max-width: 992px) {
  .services-main {
   &__top {
     padding: 185px 0 30px;
     &-link:not(:first-child) {
       margin-left: 35px;
     }
   }
  }
}

@media screen and (max-width: 768px) {
  .services-main {
    &__top {
      background: #EBA707;
      padding: 90px 0 20px;
      &-inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      &-links {
        flex-direction: column;
      }
      &-link {
        display: block;
        margin-right: auto;
        color: #FFFFFF;
        &:not(:first-child) {
          margin-top: 10px;
          margin-left: 0;
        }
        &:hover {
          color: #000;
        }
        &:after {
          display: none;
        }
        &--active {
          color: #000;
          &:after {
           display: none;
          }
        }
      }
      &-btn {
        display: block;
      }
    }
    &__menu {
      display: block;
      position: absolute;
      z-index: 7;
      top: -100%;
      left: 0;
      width: 100%;
      height: 100vh;
      background: #323233;
      padding: 80px 15px 30px;
      transition: all 0.8s;
      overflow-x: auto;
      &::-webkit-scrollbar {
        width: 5px;
      }
      &::-webkit-scrollbar-track {
        background: #b7b7b7;
        border-radius: 3px;
      }
      &::-webkit-scrollbar-thumb {
        background: #EBA707;
        border-radius: 3px;
      }
      &--active {
        top: 0;
      }
    }
  }
  .services-main {
    padding: 15px 0 25px;
    &__title {
      h2 {
        br {
          display: none;
        }
        &:after {
          width: 1px;
        }
      }
    }
  }
  .print {
    background: #fff;
    padding: 25px 0;
    &__title {
      grid-column: 1/3;
      grid-row: 1;
      justify-content: start;
      text-align: start;
      padding: 0 0 0 25px;
      h2 {
        padding: 0 0 0 15px;
        br {
          display: none;
        }
        &:after {
          left: 0;
          width: 1px;
          height: 100%;
        }
      }
    }
  }
  .printing {
    padding: 25px 0 15px;
    &__title {
      h2 {
        &:after {
          width: 1px;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .services-main {
    &__top {
      &-inner {
        flex-direction: column;
        align-items: start;
      }
      &-btn {
        order: -1;
        margin: 0 0 15px auto;
      }
    }
    &__menu-close {
      &--active {
        top: -50px;
      }
    }
  }
}

@media screen and (max-width: 375px) {
  .services-main {
    &__title {
      grid-column: 1/2;
      padding-left: 0;
    }
  }
  .print {
    &__title {
      grid-column: 1/2;
      padding-left: 0;
    }
  }
  .printing {
    &__title {
      grid-column: 1/2;
      padding-left: 0;
    }
  }
}
