.footer {
  background: #151515;
  padding: 50px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__right {
    display: flex;
    justify-content: space-between;
  }
  &__logo {
    display: block;
    width: 207px;
    img {
      display: block;
    }
  }
  &__title {
    font-weight: 500;
    font-size: 18px;
    line-height: 120%;
    text-transform: uppercase;
    color: #797979;
    display: none;
  }
  &__slider {
    max-width: 620px;
    text-align: center;
  }
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
  }
  &__link {
    font-weight: 500;
    font-size: 16px;
    line-height: 120%;
    color: #EBA707;
    position: relative;
    &:not(:first-child) {
      margin-left: 62px;
    }
    &:after {
      content: '';
      position: absolute;
      left: -10px;
      bottom: -1px;
      width: 0;
      height: 2px;
      background: #EBA707;
      transition: all 0.5s;
    }
    &:hover {
      color: #fff;
      &:after {
        width: calc(100% + 20px);
      }
    }
    &--active {
      color: #fff;
      &:after {
        content: '';
        position: absolute;
        left: -10px;
        bottom: -1px;
        width: calc(100% + 20px);
        height: 2px;
        background: #EBA707;
      }
    }
  }
  &__connection {
    text-align: end;
    &-bottom {
      display: flex;
      align-items: center;
      margin-top: 15px;
    }
  }
  &__select {
    display: inline-block;
    margin-right: 12px;
    .list {
      left: unset;
      right: 0;
    }
  }
  .nice-select {
    height: auto;
    font-size: 12px;
    line-height: 120%;
    font-weight: 400;
  }
  &__tel {
    font-size: 14px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 10px;
    display: block;
    &:hover {
      color: #EBA707;
    }
  }
  &__location {
    font-size: 12px;
    line-height: 120%;
    margin-top: 10px;
    display: block;
    &:hover {
      color: #EBA707;
    }
  }
  &__website {
    font-size: 14px;
    line-height: 120%;
    text-decoration-line: underline;
    color: #EBA707;
  }
  &__socials {
    display: flex;
    align-items: center;
    margin-left: 23px;
  }
  &__social {
    &:not(:first-child) {
      margin-left: 18px;
    }

  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    display: flex;
    align-items: center;
    height: auto;
    img {
      width: 100%;
      height: auto;
    }
  }
}

@media screen and (max-width: 1250px) {
  .footer {
    &__inner {
      display: block;
    }
    &__center {
      margin-top: 30px;
    }
    &__right {
      margin-top: 30px;
    }
    &__logo {
      margin: 0 auto;
    }
    &__list {
    }
    &__slider {
      margin: 0 auto;
    }
    &__connection {
      margin: 0 auto;
      text-align: center;
    }
    &__select {
      margin-right: 0;
      .list {
        left: 0;
        right: unset;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer {
    padding: 30px 0;
    &__logo {
      display: none;
    }
    &__title {
      display: block;
      text-align: center;
    }
    &__center {
      display: none;
    }
    &__right {
      margin-top: 15px;
    }
    &__connection {
      margin: 0;
      width: 100%;
      &-top {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      &-bottom {
        justify-content: center;
        margin-top: 15px;
      }
    }
    .nice-select {
      font-size: 18px;
    }
    &__info {
      margin-left: 60px;
    }
  }


  @media screen and (max-width: 375px) {
  .footer {
    &__info {
      margin-left: 30px;
    }
  }}
}