.select_selected {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 100%;
    width: 13px;
    height: 100%;
  }
}
.display2 {
  h2 {
    font-weight: 300;
    font-size: 60px;
    line-height: 120%;
    position: relative;
    padding-left: 28px;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      width: 2px;
      height: calc(100% + 20px);
      transform: translate(0, -50%);
      background: #FFFFFF;
    }
  }
}
.display3 {
  font-weight: 700;
  font-size: 38px;
  line-height: 120%;
}
.btn-yellow {
  font-size: 11px;
  line-height: 120%;
  background: #EBA707;
  border-radius: 29px;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  &:hover {
    background: #000;
    border: 1px solid #EBA707;
  }
}
.btn-gray {
  background: #7A7A7A;
  border-radius: 29px;
  border: 1px solid #FFFFFF;
  font-size: 11px;
  line-height: 120%;
  text-transform: uppercase;
  letter-spacing: 0.12em;
  color: #FFFFFF;
  &:hover {
    background: #EBA707;
  }
}
.nice-select {
  background: transparent !important;
  border: none;
  color: #fff;
  display: inline-block;
  width: auto;
  padding: 0;
  &:after {
    background-image: url("../img/arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 8px;
    height: 4px;
    top: 50%;
    right: -12px;
    margin-top: -2px;
    transform: rotate(0deg)
  }
  .list {
    background: #fff;
    border-radius: 0;
    width: auto;
    min-width: 100%;
    li {
      color: #000;
      padding: 10px;
      font-size: 12px;
    }
  }
  .option.selected {
    font-weight: 400;
  }
}
.nice-select .option:hover, .nice-select .option.focus, .nice-select .option.selected.focus {
  background: rgba(0,0,0,.1);
}
.nice-select.open:after {
  transform: rotate(180deg) ;
}
.offers-cards {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 18px;
}
.offers-card {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    z-index: 2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  &:before {
    content: '';
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    transition: all 0.5s;
  }
  &:hover {
    &:before {
      opacity: 1;
    }
  }
  &__desc {
    position: relative;
    z-index: 4;
  }
  &__img {
    width: 100%;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__icon {
    position: absolute;
    left: 25px;
    bottom: 25px;
    width: 82px;
    height: auto;
    display: flex;
    img {
      width: auto;
      height: auto;
    }
  }
  &__title {
    font-weight: 300;
    font-size: 32px;
    text-align: end;
    position: absolute;
    left: 25px;
    right: 25px;
    bottom: 25px;
    span {
      font-size: 18px;
      line-height: 120%;
      font-weight: 400;
      display: block;
      margin-top: 5px;
    }
  }
}
