.main {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  position: relative;
  padding: 200px 0 90px;
  &:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.86);
    backdrop-filter: blur(6px);
  }
  &__inner {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
    padding-left: 93px;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
    margin-left: 50px;
  }
  &__img {
    img {
      width: 100%;
      height: auto;
    }
  }
  &__subtitle {
    font-size: 38px;
    line-height: 120%;
  }
  &__desc {
    font-size: 20px;
    line-height: 120%;
    margin-top: 30px;
    max-width: 500px;
    p {
      &:not(:first-child) {
        margin-top: 20px;
      }
    }
  }
  &__btn {
    width: 196px;
    height: 36px;
    margin-top: 30px;
  }
}
.advantages {
  padding: 60px 0;
  background: #323232;
  &__title {
    display: flex;
    justify-content: center;
    text-align: center;
    text-transform: uppercase;
    h3 {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -190px;
        transform: translate(0, -50%);
        width: 165px;
        height: 3px;
        background: #EBA707;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        right: -190px;
        transform: translate(0, -50%);
        width: 165px;
        height: 3px;
        background: #EBA707;
      }
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    gap: 40px;
    margin-top: 30px;
  }
  &__item {
    text-align: center;
    &-icon {
      width: 60px;
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #fff;
      border-radius: 50%;
      margin: 0 auto 20px;
      &:hover {
        img {
          animation-duration: 1.5s;
          animation-iteration-count: infinite;
          animation-name: bounce-1;
          animation-timing-function: linear;
        }
      }
    }
    &-desc {
      font-size: 16px;
      line-height: 120%;
      max-width: 170px;
      margin: 0 auto;
    }
  }
}
@keyframes bounce-1 {
  0%   { transform: translateY(0); }
  50%  { transform: translateY(-50%); }
  100% { transform: translateY(0); }
}
.offers {
  padding: 40px 0;
  background: #F5F5F5;
  &__title {
    display: flex;
    justify-content: center;
    text-align: center;
    text-transform: uppercase;
    color: #7A7A7A;
    margin-bottom: 50px;
    h3 {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        left: -190px;
        transform: translate(0, -50%);
        width: 165px;
        height: 3px;
        background: #AFAFAF;
      }
      &:before {
        content: '';
        position: absolute;
        top: 50%;
        right: -190px;
        transform: translate(0, -50%);
        width: 165px;
        height: 3px;
        background: #AFAFAF;
      }
    }
  }
  &__btn {
    width: 196px;
    height: 36px;
    margin: 25px 0 0 auto;
  }
}
.connection {
  background: #606060;
  padding: 50px 0;
  &__inner {
    padding: 0 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__subtitle {
    font-weight: 300;
    font-size: 22px;
    line-height: 120%;
    margin-top: 5px;
  }
  &__btn {
    width: 254px;
    height: 48px;
    margin-left: 30px;
  }
}

@media screen and (max-width: 1300px) {
  .main {
    &__inner {
      padding-left: 30px;
    }
  }
}

@media screen and (max-width: 1250px) {
  .main {
    &__inner {
      padding-left: 0;
    }
    &__left {
      width: 470px;
      margin-right: 40px;
    }
    &__right {
      width: calc(100% - 510px);
      margin-left: 0;
    }
  }
  .advantages {
    &__items {
      gap: 30px;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__inner {
      display: block;
    }
    &__left {
      width: 100%;
      margin: 0 0 30px;
    }
    &__right {
      width: 100%;
    }
    &__img {
      max-width: 350px;
    }
  }
  .advantages {
    &__title {
      h3 {
        &:after {
          width: 100px;
          height: 2px;
          left: -120px;
        }
        &:before {
          width: 100px;
          height: 2px;
          right: -120px;
        }
      }
    }
    &__items {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
  .offers {
    &__title {
      h3 {
        &:after {
          width: 100px;
          height: 2px;
          left: -120px;
        }
        &:before {
          width: 100px;
          height: 2px;
          right: -120px;
        }
      }
    }
  }
  .connection {
    &__inner {
      padding: 0;
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    padding: 120px 0 50px;
    &__left {
      margin: 0 0 30px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__desc {
      font-size: 14px;
    }
  }
  .advantages {
    padding: 40px 0;
    &__title {
      h3 {
        &:after,
        &:before {
          display: none;
        }
      }
    }
    &__item {
      &-desc {
        font-size: 14px;
      }
    }
  }
  .offers {
    &__title {
      margin-bottom: 30px;
      h3 {
        &:after,
        &:before {
          display: none;
        }
      }
    }
    &__btn {
      margin: 25px auto 0;
    }
  }
  .connection {
    padding: 40px 0;
    &__inner {
      flex-direction: column;
      justify-content: center;
      text-align: center;
    }
    &__subtitle {
      font-size: 18px;
      margin-top: 15px;
    }
    &__btn {
      margin: 25px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .main {
    &__subtitle {
      color: #EBA707;
    }
  }
  .advantages {
   display: none;
  }
}