@media screen and (min-width: 768px) {
  .height {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
  }
}
.header {
  position: absolute;
  z-index: 7;
  top: 0;
  left: 0;
  width: 100%;
  &-top {
    background: #EBA707;
    padding: 12px 0;
    &__content {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__right {
      display: flex;
      align-items: center;
    }
    &__socials {
      display: flex;
      align-items: center;
    }
    &__soacial {
      &:not(:first-child) {
        margin-left: 22px;
      }
      &:hover {
        svg {
          path {
            fill: #000;
          }
        }
      }
    }
    &__tel {
      font-weight: 700;
      font-size: 14px;
      line-height: 120%;
      margin-left: 40px;
      &:hover {
        color: #000;
      }
    }
  }
  &-bottom {
    background: #0C0C0C;
    padding: 30px 0 30px;
    &__inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
    &__logo {
      display: block;
      width: 207px;
      height: auto;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &__list {
      display: flex;
      align-items: center;
    }
    &__link {
      font-weight: 500;
      font-size: 16px;
      line-height: 120%;
      color: #EBA707;
      position: relative;
      &:not(:first-child) {
        margin-left: 50px;
      }
      &:after {
        content: '';
        position: absolute;
        left: -10px;
        bottom: -2px;
        width: 0;
        height: 2px;
        background: #EBA707;
        transition: all 0.5s;
      }
      &:hover {
        color: #fff;
        &:after {
          width: calc(100% + 20px);
        }
      }
      &--active {
        color: #fff;
        &:after {
          content: '';
          position: absolute;
          left: -10px;
          bottom: -2px;
          width: calc(100% + 20px);
          height: 2px;
          background: #EBA707;
        }
      }
    }
    &__burger {
      width: 22px;
      height: 16px;
      position: relative;
      z-index: 11;
      cursor: pointer;
      display: none;
      span {
        height: 1.8px;
        position: absolute;
        background: #EBA707;
        border-radius: 5px;
        transition: all 0.5s;
        &:nth-child(1) {
          top: 0;
          left: 0;
          width: 100%;
        }
        &:nth-child(2) {
          top: 50%;
          left: 0;
          transform: translate(0, -50%);
          width: 100%;
        }
        &:nth-child(3) {
          left: 0;
          bottom: 0;
          width: 100%;
        }
      }
    }
    &__tel {
      display: none;
    }
  }
}

@media screen and (max-width: 992px) {
  .header {
    &-bottom {
      &__logo {
        width: 150px;
      }
      &__link {
        &:not(:first-child) {
          margin-left: 35px;
        }
        &:after {
          left: -7px;
        }
        &:hover {
          &:after {
            width: calc(100% + 14px);
          }
        }
        &--active {
          &:after {
            left: -7px;
            width: calc(100% + 14px)
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    position: fixed;
    &-top {
      display: none;
    }
    &-bottom {
      padding: 18px 0;
      &__inner {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
      }
      &__right {
        position: absolute;
        z-index: 10;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100vh;
        background: #323233;
        flex-direction: column;
        padding: 80px 15px 30px;
        transition: all 0.5s;
        &--active {
          left: 0;
        }
      }
      &__list {
        flex-direction: column;
      }
      &__link {
        font-size: 14px;
        &:not(:first-child) {
          margin: 50px 0 0 0;
        }
        &:after {
          left: -75px;
          bottom: -7px;
        }
        &:hover {
          &:after {
            width: calc(100% + 150px);
          }
        }
        &--active {
          &:after {
            left: -75px;
            bottom: -7px;
            width: calc(100% + 150px);
          }
        }
      }
      &__burger {
        display: block;
        &--active {
          span {
            &:nth-child(1) {
              transform: rotate(-45deg);
              top: 50%;
            }
            &:nth-child(2) {
              display: none;
            }
            &:nth-child(3) {
              transform: rotate(45deg);
              top: 50%;
            }
          }
        }
      }
      &__tel {
        display: block;
        text-align: end;
        margin-left: auto;
        padding: 5px;
      }
      &__logo {
        width: 110px;
        margin: 0 auto;
      }
    }
  }
}